.n-wrapper {
	height: 10vh;
	display: flex;
	justify-content: space-between;
	margin-bottom: 2rem;
	top: 0;
}
.n-left {
	flex: 1;
	align-items: center;
	justify-content: left;
	display: flex;
	gap: 2rem;
}

.n-name {
	font-size: 1.3rem;
	font-weight: bold;
}
.n-right {
	flex: 1;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	font-weight: 400;
}

.n-list {
	flex: 10;
}
.n-list > ul {
	display: flex;
	gap: 2rem;
	margin-left: 11rem;
}

.n-list > ul :hover {
	color: var(--orange);
	cursor: pointer;
}

.n-button {
	flex: 2;
}

@media screen and (max-width: 480px) {
	.n-list {
		display: none;
	}
}
